.page-wrapper {
  margin-left: 15vw;
  width: calc(100% - 15vw);
  box-sizing: border-box;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}


#tSearch {
  display: block;
  min-height: 100vh;
  max-height: 100%;
}

/* Search bar */
.teacher-search-form {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  width: 100%;
  margin: 3vh 0 10px;
  flex-wrap: nowrap;
}

.teacher-search-form input,
.teacher-search-form select {
  min-width: 170px;
  padding: 8px 10px;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 0.9rem;
}

.teacher-search-form button {
  padding: 8px 14px;
  border-radius: 6px;
  font-size: 0.9rem;
  white-space: nowrap;
  border: none;
  cursor: pointer;
  font-weight: 600;
}

.teacher-search-form button:nth-of-type(1) {
  background: #2575fc;
  color: #fff;
}

.teacher-search-form button:nth-of-type(2) {
  background: #666;
  color: #fff;
}

.teacher-search-form .add-btn,
.teacher-search-form button:nth-of-type(3) {
  background: #6a11cb;
  color: #fff;
}

/* Table */
.teacherData {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 20px;
  justify-content: center;
}

.teacher-table {
  width: 60%;
  border-collapse: collapse;
  font-family: inherit;
  font-size: 0.95rem;
  background-color: #ffffff;
}

.teacher-table thead {
  border-bottom: 2px solid #e0e0e0;
}

.teacher-table th,
.teacher-table td {
  padding: 8px 12px;
  text-align: left;
}

.teacher-table th {
  font-weight: 600;
  color: #444;
}

.teacher-table tbody tr:nth-child(even) {
  background-color: #f9f9f9ee;
}

.teacher-table tbody tr:nth-child(odd) {
  background-color: #f9f9f963;
}

.teacher-table tbody tr:hover {
  background-color: #f1f1f1;
}

.view-btn {
  padding: 4px 14px;
  font-size: 0.8rem;
  border-radius: 999px;
  border: 1px solid #2575fc;
  background: #ffffff;
  color: #2575fc;
  cursor: pointer;
  transition: all 0.2s ease;
}

.view-btn:hover {
  background: #2575fc;
  color: #ffffff;
}

.view-btn:active {
  transform: scale(0.97);
}

hr {
  border: 0;
  height: 1px;
  background: #333;
  background-image: linear-gradient(to right, #ccc, #333, #ccc);
  width: auto;
  margin-top: 5vh;
}
